import React from 'react'
import classes from './DrawerTool.css'

const Drawer = (props) => {
    return(
        <div className={classes.DrawerToggle} onClick={props.clicked}>
            <div></div>
            <div></div>
            <div></div>
        </div>
    )
}

export default Drawer


